<template>
  <div id="adjuntos">
    <v-data-table
        :headers="headers"
        :items="dataAdjuntos"
        class="elevation-1"
    >
      <template v-slot:item.descripcionestado="{ item }">
        <span :class="[{ColorVBFalse:(item.estado === 'REZ')}, {ColorVBTrue:(item.estado !== 'REZ')}]">{{ item.descripcionestado }}
        </span>
      </template>

      <template v-slot:item.carguearchivo="{ item }">
        <v-chip v-if="item.estado == 'VAL'"
            class="ma-2"
            @click="openInNewTab(item.file_path, item.file_type)"
            color="green darken-2"
            text-color="white"
            style="margin: 5px"
        >
            {{item.file_name.substring(0, 50)}}
        </v-chip>
        <v-btn text v-else>
          <AddFilesToStorage
            :item="item"
            placeholder="Cargar Archivo"
            dense
            hide-details
            prepend-icon
            @remove-file="fileReset++"
            :key="fileReset"
            @file-changed="fileReset++"
          />
        </v-btn>
      </template>
    </v-data-table>
    <br>
      <v-btn
        class="mx-2"
        absolute
        bottom
        left
        fab
        dark
        color="primary"
        @click="guardarDocumentos"
        style="bottom:70px;"
      >
        <v-icon dark>
          save
        </v-icon>
      </v-btn>
      <v-btn
        class="mx-2"
        absolute
        bottom
        left
        dark
        color="primary"
        @click="enviarJuridica"
        style="bottom:30px;"
        v-if="enviado == 0 && totalarchivos == totalcargados"
      >
        Enviar
      </v-btn>
    <br>
    </div>
</template>
<script>
import AddFilesToStorage from '@/components/shared/AddFilesToStorage'
import { anunciosProcAdquisicion } from '@/services/api'
import FileExchange from '../../services/FileExchange'
import { b64toBlobUrl } from '../../services/utilities'
export default {
  props: ['idregistro', 'tipodoc', 'idpropconv'],
  components: { AddFilesToStorage },
  data () {
    return {
      dataAdjuntos: [],
      proponenteId: '',
      totalarchivos: 0,
      totalcargados: 0,
      enviado: 0,
      headers: [
        { text: 'Descripción', align: 'left', value: 'descripciondocumento', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Estado', align: 'left', sortable: false, value: 'descripcionestado', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Observación', align: 'left', sortable: false, value: 'observacion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Archivo', align: 'left', sortable: false, value: 'carguearchivo', class: 'subtitle-1 font-weight-bold black--text' }
      ],
      resetComponent: 0,
      fileReset: 0
    }
  },
  async mounted () {
    this.traeDocumentacionFaltante()
    this.getData()
  },
  methods: {
    async traeDocumentacionFaltante () {
      await anunciosProcAdquisicion.agregaDocumentoContratoRegistro({
        registro: this.idregistro,
        tipo: this.tipodoc,
        idpropconv: this.idpropconv
      })
    },
    async getData () {
      this.dataAdjuntos = []
      const { data } = await anunciosProcAdquisicion.buscarAdjuntosDocumentoContratoRegistro({
        registro: this.idregistro,
        tipo: this.tipodoc,
        idpropconv: this.idpropconv
      })
      this.dataAdjuntos = data.archivos
      this.totalarchivos = this.dataAdjuntos.length
      this.totalcargados = 0
      for (let index = 0; index < this.totalarchivos; index++) {
        const element = this.dataAdjuntos[index]
        console.log(element.estado)
        if (element.estado === 'EVA') {
          this.enviado = 1
          break
        }
        if (element.file_name !== '' && element.file_path !== '') {
          this.totalcargados += 1
        }
      }
    },
    actAdjuntos (item) {
      if (item.file_path) {
        item.nombre = item.file_name
        item.path = item.file_path
      } else {
        item.nombre = ''
        item.path = ''
      }
      this.resetComponent++
    },
    async guardarDocumentos () {
      var dataadjuntos = []
      for (let index = 0; index < this.adjuntos.length; index++) {
        const element = this.adjuntos[index]
        dataadjuntos.push({ idpproconvreg: this.idregistro, idpproconvdoc: element.proconvdoc, file_name: element.file_name, file_path: element.file_path, file_size: element.file_size, file_type: element.file_type })
      }
      const { data } = await anunciosProcAdquisicion.guardardatosAdjuntosDocContratacion({
        dataadjuntos: dataadjuntos
      })
      if (data.tipo) {
        if (data.tipo === 'success') {
          this.$toasted.success(data.mensaje, { icon: 'done', duration: 5000 })
          this.getData()
        } else {
          this.$toasted.error(data.mensaje, { icon: 'error', duration: 5000 })
        }
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
    },
    async enviarJuridica () {
      const { data } = await anunciosProcAdquisicion.enviarDocumentosContratoJuridica({
        registro: this.idregistro,
        tipo: this.tipodoc,
        propconvid: this.idpropconv
      })
      if (data) {
        this.$toasted.success('Se hizo el envío al área jurídica con éxito', { icon: 'done', duration: 5000 })
        this.getData()
      } else {
        this.$toasted.error('Error desconocido', { icon: 'error', duration: 5000 })
      }
    },
    async openInNewTab (url, type) {
      try {
        const result = await FileExchange.getFileFromStorage(url)
        const fileData = result.data
        const urlBlob = b64toBlobUrl(fileData, type, '')
        var win = window.open(urlBlob, '_blank')
        win.focus()
      } catch (error) {
        console.log(error)
      }
    }
  },
  computed: {
    adjuntos: {
      get () {
        return this.dataAdjuntos
      },
      set (val) {
        this.adjuntos = val
      }
    }
  },
  watch: {
    idregistro (val) {
      this.dataAdjuntos = []
      this.traeDocumentacionFaltante()
      this.getData()
    },
    tipodoc (val) {
      this.traeDocumentacionFaltante()
      this.getData()
    },
    idpropconv (val) {
      this.traeDocumentacionFaltante()
      this.getData()
    }
  }
}
</script>
<style>
  .anexosClass {
    min-width: 300px;
  }
  #adjuntos .v-label {
    font-size: .70rem;
  }
  #adjuntos .v-label .inFiles{
    font-size: .95rem;
  }
  .ColorVBTrue{
    color:blue;
    font-weight: bold;
  }
  .ColorVBFalse{
    color:red;
    font-weight: bold;
  }
</style>
